body {
    width: 100%;
    height: 100%;
    background-color: #1a1a1a;
}

.wrapper {
    height: 100vh;
    width: 100%;
    display:inline-flex;
    background-color: #1a1a1a;
}

h1 {
    font-size: 1.5vw;
}

.button {
    height: 100%;
    width: 100%;
    background-color: #242424;
    border: none;
    margin-top: 1%;
    font-size: 100%;
}

.sidebar {
    width: 20%;
    height: 100vh;
    background: #1a1a1a;
    background-color: #1a1a1a;
    position:absolute;
    margin-top:2%;

}

ul {
    position: absolute;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    list-style: none;
    background-color: #1a1a1a;
}

li {
    position: relative; 
    display: inline-block;
    width:  99%;
    height: 10vh;
    margin-top:5%;
    
}

.sidebar-header {
    height: 9%;
    position: relative;
    font-size: 1.2em;
    background: linear-gradient(to right bottom, #2f3441 50%, #212531 50%);
    color:white;
    justify-content: center;
    padding-top: 5%;
    text-align: center;
}

.shoppingList {
    margin-left:20%;
    width: 80%;
    height: 100%;
    position:absolute;
    margin-top: 2%;

}
.searchDiv {
    width:100%;
    height: 9%;
    background-color: #1a1a1a;
    display: in-line;
}
.searchBar {
    width: 20%;
    height: 9%;
    margin-left: 1%;
    position: absolute;
    font-size: 3vh;
    
}
.searchbarButton {
    width:25%;
    height: 100%;
    min-height: 100%;
    font-size: 1em;
    float: left;
    background-color: #303030;
    border: none;
    margin-left:0.5vw;

}

.loginModal {
    width:80vw;
    height: 30vh;
    font-size: 1.5vw
}

.headerbutton {
    width: 25%;
    height: 100%;
    max-width: 5em;
    margin-left: 1%;
    background-color: #303030;
    border:none;
    float: left;
    position:relative;
    text-align: center;

}

.header {
    width: 100%;
    height: 100%;
    background-color: #1a1a1a;
    font-size: 1em;
    color:white;
    display:in-line;
    text-align: center;

}

div#root {
    width: 100%;
    height: 20vh;
}

.link {
    color:black;
}